* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.header {
  color: white;
  font-size: 32px;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  background-color: blueviolet;
}
.navbar {
  height: 60px;
  background-color: black;
  width: 100%;
}
.navbar ul {
  margin: 0 0 0 50px;
  font-size: 22px;
  height: 60px;
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 40px;
  align-items: center;
}
.navbar ul li a {
  color: blueviolet;
  text-decoration: none;
}
.row {
  display: flex;
  flex-wrap: wrap;
}
.sidebar {
  padding: 20px;
  height: auto;
  flex: 70%;
}
.sidebar .img {
  margin-top: 50px;
  height: 400px;
  width: 100%;
}
.sidebar .box1 {
  margin-top: 20px;
}
.sidebar .box2 {
  margin-top: 20px;
}

.contentbar {
  padding: 20px;
  margin-top: 50px;
  flex: 30%;
}
.contentbar .contentimg {
  margin-top: 40px;
  height: 150px;
  width: 400px;
}
.footer {
  height: 100px;
  width: 100%;
  background-color: blueviolet;
  color: white;
  font-size: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}
